@import url(https://fonts.googleapis.com/css?family=Oswald:400,500,600);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #777;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.667;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  position: fixed;
  width: 100%;
  opacity: 0.9;
  z-index: 10;
}
.dummy-head {
  width: 100%;
  height: 70px;
}
.header__brand__img {
  height: 50px;
  margin: 5px 20px;
}

.products__main-title {
  text-align: center;
  padding: 10px;
  padding-top: 30px;
  /* color: #0d6efd; */
  color: #8cbaff; /*temp*/
  display: flex;
  justify-content: center;
  align-items: center;
}

.products__main-title:hover {
  transition: 0.3s;
}

.productsTitle div {
  width: 10px;
  height: 10px;
  background-color: red;
}
.products__products-container {
  display: flex;
  flex-wrap: wrap;
}
.products__products-container__card {
  width: 18rem;
  color: black;
  margin: 20px;
  transition: all 0.2s ease;
}

.products__products-container__card:hover {
  transition: 0.3s;
  /* transform: translateX(10px); */
  -webkit-transform: rotate3d(-1, 1, 0, 19deg) translateY(-10px);
          transform: rotate3d(-1, 1, 0, 19deg) translateY(-10px);
  /* opacity: 0.7; */
}
.products__products-container__card__card-body {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.products__products-container__card__card-body h5 {
  font-family: "Oswald", sans-serif;
}
.products__products-container__card__card-body p,
h6 {
  color: #777;
  font-family: "Roboto", sans-serif;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
}

.footer {
  padding: 50px 0 20px 0;
  background-color: #35404f;
  color: #878c94;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .title {
  text-align: left;
  color: #fff;
  font-size: 25px;
}
.footer__row {
  display: flex;
  justify-content: space-between;
}
.footer .payment {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
}
.footer .payment li {
  list-style-type: none;
}
.footer .payment li a {
  text-decoration: none;
  color: #fff;
  float: left;
  font-size: 25px;
  padding: 10px 10px;
}
.footer__buy_with_meta_mask {
  width: 240px;
  padding: 10px 20px;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  opacity: 0.8;
}

.footer__buy_with_meta_mask img {
  width: 100%;
}
.footer__buy_with_meta_mask:hover {
  opacity: 1;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.payment-div {
  width: 25%;
}
@media screen and (max-width: 1054px) {
  .payment-div {
    width: 50%;
  }
}
@media screen and (min-width: 1221px) {
  .footer__container {
    max-width: 1200px;
    padding: 0px 40px;
  }
}

.productdetail__back-button {
  width: 50px;
  height: 50px;
  display: flex;
  margin: 10px;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.904);
  color: black;
  z-index: 1;
  border-radius: 2px;
  cursor: pointer;
  position: fixed;
  transition: all 0.2s ease;
}
.productdetail__back-button:hover {
  background-color: rgb(221, 221, 221);
}
.productdetail__product-detail-area {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.productdetail__product-detail-area__container {
  display: flex;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
}
.productdetail__product-detail-area__container__image {
  /* max-width: 500px;/ */
  flex: 0.5 1;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.productdetail__product-detail-area__container__image img {
  width: 100%;
}
.productdetail__product-detail-area__container__products-text {
  flex: 0.5 1;
  /* width: 100%; */
  padding: 5rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.productdetail__product-detail-area__container__products-text h3 {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 500;
  color: #222;
  margin-bottom: 20px;
  line-height: 1.1;
}

.productdetail__product-detail-area__container__products-text h2 {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: #384aeb;
  margin-bottom: 10px;
}

.productdetail__product-detail-area__container__products-text h2 {
  padding-top: 20px;
  border-top: 1px dotted #eeeeee;
  border-bottom: 1px dotted #eeeeee;
  margin-top: 20px;
  padding-bottom: 26px;
  margin-bottom: 0;
}
.buy-button {
  padding: 10px 20px;
}
@media screen and (max-width: 991px) {
  .productdetail__product-detail-area__container {
    flex-direction: column;
  }
  .productdetail__product-detail-area__container__image {
    width: 100%;
    padding: 20px 50px;
    max-width: 990px;
  }
}

/*---------- Ratings --------*/
.productdetail__product-review-area {
  width: 100%;
  display: flex;
  justify-content: center;
}
.productdetail__product-review-area__container {
  width: 100%;
  max-width: 1200px;
  padding: 20px;
}
.productdetail__product-review-area__container__content {
  display: flex;
}
.productdetail__product-review-area__container__content__reviews {
  flex: 0.5 1;
}
.productdetail__product-review-area__container__content__create-review-box {
  flex: 0.5 1;
}
.productdetail__product-review-area__container__content__reviews__overallreview {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 90px;
}
.overallreview-container {
  width: 100%;
  height: 100%;
  background: #f1f6f7;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: "Oswald", sans-serif;
}
.overallreview-container h5 {
  color: #333333;
  margin-bottom: 0px;
  font-size: 24px;
  line-height: 1.1;
}
.overallreview-container h4 {
  font-size: 48px;
  font-weight: bold;
  color: #384aeb;
  line-height: 1.3;
}
.overallreview-container h6 {
  font-family: "Roboto", sans-serif;
  margin-bottom: 0px;
  font-size: 14px;
  color: #555555;
  font-weight: normal;
}

/* .productdetail__product-review-area__container__content__reviews__all-reviews {
  overflow-y: scroll;
} */

.productdetail__product-review-area__container__content__reviews__all-reviews__review-card {
  color: #777;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.667;
  display: flex;
  flex-direction: column;
}
.review-card__name_details {
  display: flex;
  width: 100%;
  padding: 10px;
}
.review-card__name_details__image {
  width: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.review-card__name_details__image img {
  width: 100%;
  border-radius: 50%;
}
.review-card__name_details__name {
  font-family: "Oswald", sans-serif;
  color: black;
  width: 100%;
  padding: 5px 10px;
  line-height: 1.3;
}

.review-card__name_details__name__date {
  padding-left: 30px;
}
.review-card__review-text {
  padding: 20px;
}
.set-stars {
  cursor: pointer;
}
.set-stars:hover {
  color: #fb9700;
}
.yellow {
  color: #fbd600;
}
.productdetail__product-review-area__container__content__create-review-box {
  padding-left: 20px;
}
.productdetail__product-review-area__container__content__create-review-box h4 {
  font-size: 20px;
  color: #222;
  margin-bottom: 20px;
  margin-top: 20px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  line-height: 1.3;
}
.productdetail__product-review-area__container__content__create-review-box
  input,
.productdetail__product-review-area__container__content__create-review-box
  textarea {
  margin: 10px;
}

@media screen and (max-width: 900px) {
  .productdetail__product-review-area__container__content {
    flex-direction: column-reverse;
  }
  .productdetail__product-review-area__container__content__create-review-box {
    padding: 20px 50px;
  }
}

.productdetail__loader {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

